<template>
  <Loader v-if="loader"/>
  <div v-else class="profile">
    <span class="caption_change__one">Личный кабинет</span>
    <span class="caption_change__two">Ваши параметры</span>
    <span class="profile_captions">Имя: {{name}}</span>

      <div class="inputs__wrap">
        <input type="text" v-model="vname" class="inputs" placeholder="Введите новое имя">
        <span @click="changeName" class="text_bottom">Изменить имя</span>
      </div>
        <span class="profile_captions">Вес: {{record.weight}} кг.</span>
    <form @submit.prevent="changeWeight">
      <div class="inputs__wrap">
        <input type="text" v-model="weight" class="inputs" placeholder="Введите новый вес" @blur="$v.weight.$touch()">
        <small
            v-if="$v.weight.$dirty && !$v.weight.numbers"
        >
          Введите число в формате 00.0
        </small>
        <span @click="changeWeight" class="text_bottom">Изменить вес</span>
      </div>
    </form>

    <span class="profile_captions">Желаемый вес: {{otherData[1].wishWeight}} кг.</span>
    <form @submit.prevent="changeWeight">
      <div class="inputs__wrap">
        <input type="text" v-model="wishWeight" class="inputs" placeholder="Введите новый желаемый вес" @blur="$v.wishWeight.$touch()">
        <small
            v-if="$v.wishWeight.$dirty && !$v.wishWeight.numbers"
        >
          Введите число в формате 00.0
        </small>
        <span @click="changeWishWeight" class="text_bottom">Изменить желаемый вес</span>
      </div>
    </form>
    <span class="profile_captions">Рост: {{otherData[0].height}} см.</span>
    <form @submit.prevent="changeWeight">
      <div class="inputs__wrap">
        <input type="text" v-model="height" class="inputs" placeholder="Введите новый рост" @blur="$v.height.$touch()">
        <small
            v-if="$v.height.$dirty && !$v.height.numbers"
        >
          Введите число в формате 00.0
        </small>
        <span @click="changeHeight" class="text_bottom">Изменить рост</span>
      </div>
    </form>
    <span @click="deleteDatabase" class="delData_btn">Удалить все записи в дневнике веса</span>
  </div>

</template>

<script>
import firebase from 'firebase/app'
import {helpers} from 'vuelidate/lib/validators'
const numbers = helpers.regex('numbers', /^[0-9]*[.]?[0-9]+$/)


export default {
  name: "Profile",
  data: () => ({
    record: [],
    otherData: [],
    loader: true,
    uid: null,
    name: '',
    vname: '',
    height: null,
    weight: null,
    wishWeight: null,
    id: ''
  }),
  validations: {
    height: {
      numbers
    },
    weight: {
      numbers
    },
    wishWeight: {
      numbers
    }
  },
  methods: {
    async changeName() {

      await firebase.database().ref(`users/${this.uid}/info`).update({name: this.vname})
      this.name = await this.$store.dispatch('getName')
      this.vname = ''

    },
    async changeWeight() {
      if (this.weight) {
        if (this.$v.$invalid) {
          this.$v.$touch()
          return
        }
      const record = await this.$store.dispatch('getValueFromDatabase')
      this.id = record.map(rec => {
        return {
          ...rec
        }
      }).reverse()[0].id
      await firebase.database().ref(`users/${this.uid}/data/${this.id}`).update({weight: this.weight})
      this.record = (await this.$store.dispatch('getValueFromDatabase')).map(rec => {
        return {...rec}}).reverse()[0]
      this.weight = null
      }
    },
    async changeWishWeight() {
      await firebase.database().ref(`users/${this.uid}/other-data/wishWeight`).update({wishWeight: this.wishWeight})
      this.otherData = await this.$store.dispatch('getOtherFromDatabase')
      this.wishWeight = null
    },
    async changeHeight() {
      await firebase.database().ref(`users/${this.uid}/other-data/height`).update({height: this.height})
      this.otherData = await this.$store.dispatch('getOtherFromDatabase')
      this.height = ''
    },
    async deleteDatabase(lastId) {
      await this.$store.dispatch('deleteDatabase', lastId)
    }
  },
  async mounted() {
    this.uid = await this.$store.dispatch('getUid')
    this.record = (await this.$store.dispatch('getValueFromDatabase')).map(rec => {
      return {...rec}}).reverse()[0]
    this.otherData = await this.$store.dispatch('getOtherFromDatabase')
    this.name = await this.$store.dispatch('getName')
    this.loader = false
  }
}
</script>

<style scoped>
  .profile {
    display: flex;
    flex-direction: column;
  }
</style>